<template>
  <div class="content-wrapper">
    <div style="margin:20px 20px 0px 20px">
      <div v-for="item in fileList" style="margin-top:5px">
        <div class="media">
          <div class="media-left  media-middle">
            <a href="javascript:void(0)" @click="view(item)">
              <img
                :src="item.fileType.indexOf('image')>-1?$store.state.domain+item.key:'http://pubresource-1252795299.cosgz.myqcloud.com/img/pub/pdf.png'"
                class="media-object media-img"/>
            </a>
          </div>
          <div class="media-body">
            <h4 class="media-heading">{{item.filename|filterFilename}}</h4><code>{{item.filesize}}字节</code>
            <div v-show="item.note">备注:{{item.note}}</div>
            <div>{{item.create_at|filterCreateAt}}</div>

          </div>
          <div class="media-right">
            <i
              class="fa fa-times fa-2x"
              style="float: right;color:darkred" @click="conDel(item)" :class="{disabled:loading}"></i>
          </div>
        </div>
        <div class='divider'></div>
      </div>
      <div class='text-center' style='padding:10px 0px;'>
        <button v-show='pageIndex<=pageCount' class='btn btn-success' :class='{disabled:loading}' style='width:200px'
                @click='init'>加载更多
        </button>
      </div>

    </div>
    <Tips :msg.sync="tips.message" title="提示" :show.sync="tips.showTips" :ok-text="tips.okText" @ok="confirm"
          @cancel="tips.showTips=false">
    </Tips>
  </div>
</template>
<style scoped>
  .divider {
    background: #f0f0f0;
    width: 100%;
    height: 1px;
    margin: 5px 0px 0px 50px;
  }

  .media-img {
    width: 64px;
    height: 64px
  }
</style>
<script>
  import Tips from './Tips.vue'
  export default {
    components: {Tips},
    data() {
      return {
        tips: {
          message: '',
          showTips: false,
          okText: '好的',
          currentType: 0,
          //正在操作的对象
          currentItem: {},
        },
        TIPS_TYPE: {
          COMMON: 0,
          DEL_FILE: 1
        },
        loading: false,
        pageIndex: 0,
        pageSize: 10,
        pageCount: 1,
        fileList: []
      }
    },
    mounted: function () {
      this.init();
    },
    filters: {
      filterCreateAt: function (date) {
        return date.replace('T', ' ').replace('Z', '').substring(0, date.lastIndexOf("."));
      },
      filterFilename: function (name) {
        return name.substring(0, name.lastIndexOf("."));
      }
    },
    methods: {
      confirm: function () {
        if (this.tips.currentType === this.TIPS_TYPE.COMMON)
          this.tips.showTips = false;
        //删除文件
        else if (this.tips.currentType === this.TIPS_TYPE.DEL_FILE) {
          this.tips.showTips = false;
          this.loading = true;
          this.delete(this.tips.currentItem);
        }
      },
      conDel: function (item) {
        this.tips.currentItem = item;
        this.tips.okText = '确认';
        this.tips.message = '确认删除此文件吗?';
        this.tips.currentType = this.TIPS_TYPE.DEL_FILE;
        this.tips.showTips = true;
      },
      delete: function (item) {
        $.ajax({
          type: 'DELETE',
          url: this.$store.state.baseUrl + '/files/' + item._id,
          success: this.deleteResult,
          error: this.deleteResult
        })
      },
      deleteResult: function (data) {
        this.loading = false;
        if (!data.error) {
          this.fileList.splice(this.fileList.indexOf(this.tips.currentItem), 1);
          return;
        }
        this.tips.message = data.error;
        this.tips.showTips = true;
        this.tips.okText = '好的';
        this.tips.currentType = this.TIPS_TYPE.COMMON
      },
      init: function () {
        this.pageIndex++;
        if (this.pageIndex > this.pageCount) {
          return;
        }
        this.loading = true;
        $.ajax({
          type: 'GET',
          url: this.$store.state.baseUrl + '/files/58de53c121e7dc3aa8e48cb8/list',
          data: {
            pageIndex: this.pageIndex,
            pageSize: this.pageSize,
          },
          success: this.initResult,
          error: this.initResult
        })
      },
      initResult: function (data) {
        this.loading = false;
        if (data && data.results.length > 0)
          this.fileList = this.fileList.concat(data.results);
        this.pageCount = data.pageCount;
        console.log(this.fileList);
      },
      previewImg: function (src) {
        this.pictureList = [];
        this.pictureList.push({
          src: src, w: document.body.clientWidth,
          h: document.body.clientHeight
        });
        //npm i vue-preview -S
        this.$preview.open(0, this.pictureList);
      },
      view: function (item) {
        if (item.fileType.indexOf('image') > -1) {
          this.$router.push({name: 'readImg', query: {imgUrl: this.$store.state.domain+item.key,fileId:item._id}})
        }
        else
          this.$router.push({name: 'readPdf', query: {pdfUrl:item.pdfKey?this.$store.state.domain+item.pdfKey:this.$store.state.domain+item.key,fileId:item._id}})
      }
    }
  }
</script>



