// index.js
import Vue from 'vue'
import Vuex from 'vuex'
import * as types from './mutation-type'
Vue.use(Vuex)


const state = {
  //浏览器是不是电脑
  isPC: true,
  baseUrl:'http://jungieve.s1.natapp.cc',
  domain:'http://omt3mqb30.bkt.clouddn.com/',
  bucket:'xunzhuresource',
}

const getters = {
  isPC: function (state) {
    return state.isPC;
  }
}
const actions = {}

const mutations = {
  //记录浏览器是不是电脑
  [types.JUDGE](state, {value}){
    state.isPC = value;
  },

}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



