<template>
  <div class="content-wrapper" style="margin-top: 20px;background-color: #ffffff!important;">

    <div :class="{ file:isPC }">
      <div style="text-align: center;vertical-align: middle">
        <button class="btn btn-default" @click="onPrevPage">上一页</button>
        <button class="btn btn-default" @click="onNextPage">下一页</button>
        <span>{{pdfConf.pageNum}}/{{pdfConf.totalNum }}</span>
        <i class="fa fa-search-plus fa-2x" aria-hidden="true" @click="plus"></i>
        <i class="fa fa-search-minus fa-2x" aria-hidden="true" @click="minus"></i>
      </div>
      <div style="overflow-x:auto;height: 900px;padding: 20px;text-align: center"
           ref="canvasParent">
        <div style="width: 100%;height: 100%;display: flex;justify-content: center;align-items: center"
             v-show="pdfConf.pageRendering">
          <bounce-loader :loading="pdfConf.pageRendering" size="120px"></bounce-loader>
        </div>
        <canvas ref="canvas" v-show="!pdfConf.pageRendering"></canvas>
      </div>
    </div>
    <div class="comment" v-if="isPC">
      <div class="box box-primary direct-chat direct-chat-primary" ref="boxHeader">
        <div class="box-header with-border">
          <h3 class="box-title">评论</h3>
        </div>
        <div class="box-body box-content" ref="boxBody" style="height:738px">
          <div class="direct-chat-messages">
            <!--<div class="direct-chat-msg">-->
            <!--<div class="direct-chat-info clearfix">-->
            <!--<span class="direct-chat-name pull-left">Yelo</span>-->
            <!--<span class="direct-chat-timestamp pull-right">01-23 2:00 pm</span>-->
            <!--</div>-->
            <!--<img class="direct-chat-img" src="../dist/img/user1-128x128.jpg" alt="Message User Image">-->
            <!--<div class="direct-chat-text">-->
            <!--你居然也看懂了这个算法？666666-->
            <!--</div>-->
            <!--</div>-->
            <!--<div class="direct-chat-msg right">-->
            <!--<div class="direct-chat-info clearfix">-->
            <!--<span class="direct-chat-name pull-right">文叔叔</span>-->
            <!--<span class="direct-chat-timestamp pull-left">01-23 2:30 pm</span>-->
            <!--</div>-->
            <!--<img class="direct-chat-img" src="../dist/img/user3-128x128.jpg" alt="Message User Image">-->
            <!--&lt;!&ndash; /.direct-chat-img &ndash;&gt;-->
            <!--<div class="direct-chat-text">-->
            <!--哈哈哈，没有没有，我装B而已-->
            <!--</div>-->
            <!--</div>-->
            <!--<div class="direct-chat-msg">-->
            <!--<div class="direct-chat-info clearfix">-->
            <!--<span class="direct-chat-name pull-left">Yelo</span>-->
            <!--<span class="direct-chat-timestamp pull-right">01-23 3:48 pm</span>-->
            <!--</div>-->
            <!--<img class="direct-chat-img" src="../dist/img/user1-128x128.jpg" alt="Message User Image">-->
            <!--<div class="direct-chat-text">-->
            <!--可以，99分-->
            <!--</div>-->
            <!--</div>-->
            <div class="direct-chat-msg right" v-for="item in commentList">
              <div class="direct-chat-info clearfix">
                <span class="direct-chat-name pull-right">{{item.commenter.nickname}}</span>
                <span class="direct-chat-timestamp pull-left">{{item.create_at|filterCreateAt}}</span>
              </div>
              <img class="direct-chat-img" :src="item.commenter.headimgurl" alt="Message User Image">
              <div class="direct-chat-text">
                {{item.comment}}
              </div>
            </div>
          </div>

        </div>
        <div class="box-footer" ref="boxFooter">
          <div class="input-group">
            <input type="text" name="message" placeholder="请输入内容 ..." class="form-control"
                   v-model='commentContent'>
                        <span class="input-group-btn"><button type="button" class="btn btn-primary btn-flat"
                                                              :class="{disabled:loading}"
                                                              @click='makeComment'>发送</button></span>
          </div>
        </div>
      </div>
    </div>


    <Popup ref='bottom' title="评论" confirmText='' :show='commentShow' @hideFun='cancelFun' @showFun='showFun'
           @cancelFun='cancelFun' @confirmFun='cancelFun' :action-show='true' :mask-close='false' v-if="!isPC">
      <div class="pop">
        <div class="box box-primary direct-chat direct-chat-primary">
          <div class="box-body">
            <div class="direct-chat-msg right" v-for="item in commentList">
              <div class="direct-chat-info clearfix">
                <span class="direct-chat-name pull-right">{{item.commenter.nickname}}</span>
                <span class="direct-chat-timestamp pull-left">{{item.create_at|filterCreateAt}}</span>
              </div>
              <img class="direct-chat-img" :src="item.commenter.headimgurl" alt="Message User Image">
              <div class="direct-chat-text">
                {{item.comment}}
              </div>
            </div>
          </div>
          <div class="box-footer" ref="boxFooter">
            <div class="input-group">
              <input type="text" name="message" placeholder="请输入内容 ..." class="form-control"
                     v-model='commentContent'>
                            <span class="input-group-btn"><button type="button" class="btn btn-primary btn-flat"
                                                                  :class="{disabled:loading}"
                                                                  @click='makeComment'>发送</button></span>
            </div>
          </div>
        </div>
      </div>
    </Popup>


    <div style="text-align: center" v-if="!isPC" class="clear">
      <button class="btn btn-success" style="margin: 0px 20px;width: 250px" @click="comment">查看评论</button>
    </div>
  </div>
</template>
<style scoped>
  body {
    background-color: #ffffff;
  }

  .file {
    float: left;
    width: 70%;
    height: 100%;
    background-color: #ffffff;
  }

  .comment {
    padding: 100px 20px 0px 20px;
    height: 900px;
    padding-top: 100px;
    text-align: center;
    float: left;
    width: 30%;
    height: 100%;
    background-color: #ffffff;
  }

  .pop {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: .14rem;
    color: #666;
  }

</style>

<script>
  import PDFJS from '../assets/pdf/pdf.js'
  import Popup from './Popup.vue'
  //https://github.com/greyby/vue-spinner
  import BounceLoader from 'vue-spinner/src/BounceLoader.vue'
  export default {
    components: {
      Popup,
      BounceLoader
    },
    data() {
      return {
        pdfConf: {
          firstTime: true,//第一次渲染
          pdfDoc: null,
          pageNum: 1,
          totalNum: 1,
          pageRendering: true, //是否正在加载pdf
          pageNumPending: null,
          scale: 1.1,
          maxscale: 1.5,//最大放大倍数
          minscale: 0.6,//最小放大倍数
          ctx: {},
          canvas: {}
        },
        commentShow: false,
        isPC: false,
        commentList: [],
        commentContent: '',
        loading: false,
      }
    },
    filters: {
      filterCreateAt: function (date) {
        return date.replace('T', ' ').replace('Z', '').substring(0, date.lastIndexOf("."));
      },
    },
    methods: {
      comment: function () {
        //如果是手机端，则从底部弹出来
        if (this.isPC == false)
          this.commentShow = true;
      },
      cancelFun: function () {
        console.log('hide popup');
        this.commentShow = false;
      },
      showFun: function () {
        console.log('show popup');
      },
      //发起评论
      makeComment: function () {
        this.loading = true;
        $.ajax({
          type: 'POST',
          url: this.$store.state.baseUrl + '/comments/file/' + this.$route.query.fileId + "/" + this.pdfConf.pageNum,
          data: {
            commenterid: '58de53c121e7dc3aa8e48cb8',
            comment: this.commentContent
          },
          success: this.makeCommentResult,
          error: this.makeCommentResult
        })
      },
      makeCommentResult: function (data) {
        this.loading = false;
        if (data._id) {
          this.getAllComment();
        }

      },
      //获取某页文件的所有评论
      getAllComment: function () {
        this.loading = true;
        $.ajax({
          type: 'GET',
          url: this.$store.state.baseUrl + '/comments/file/' + this.$route.query.fileId + "/" + this.pdfConf.pageNum + "/list?pageIndex=1&pageSize=10000",
          success: this.getAllCommentResult,
          error: this.getAllCommentResult
        })
      },
      getAllCommentResult: function (data) {
        this.loading = false;
        if (data.results)
          this.commentList = data.results;
      },
      /**
       * Get page info from document, resize canvas accordingly, and render page.
       * @param num Page number.
       */
      renderPage: function (num) {
        //获取当页评论
        this.getAllComment();
        this.pdfConf.pageRendering = true;
        // Using promise to fetch the page
        let that = this;
        this.pdfConf.pdfDoc.getPage(num).then(function (page) {

          var viewport = page.getViewport(that.pdfConf.scale);
          that.pdfConf.canvas.height = viewport.height;
          that.pdfConf.canvas.width = viewport.width;

          // Render PDF page into canvas context
          var renderContext = {
            canvasContext: that.pdfConf.ctx,
            viewport: viewport
          };
          var renderTask = page.render(renderContext);

          let those = that;
          // Wait for rendering to finish
          renderTask.promise.then(function () {
            those.pdfConf.pageRendering = false;
            if (those.pdfConf.pageNumPending !== null) {
              // New page rendering is pending
              those.renderPage(those.pdfConf.pageNumPending);
              those.pdfConf.pageNumPending = null;
            }
          });
        });
      },
      /**
       * If another page rendering in progress, waits until the rendering is
       * finised. Otherwise, executes rendering immediately.
       */
      queueRenderPage: function (num) {
        if (this.pdfConf.pageRendering) {
          this.pdfConf.pageNumPending = num;
        } else {
          this.renderPage(num);
        }
      },
      onPrevPage: function () {
        if (this.pdfConf.pageNum <= 1)
          return;
        this.queueRenderPage(--this.pdfConf.pageNum);
      },
      onNextPage: function () {
        if (this.pdfConf.pageNum >= this.pdfConf.pdfDoc.numPages)
          return;
        this.queueRenderPage(++this.pdfConf.pageNum);
      },
      plus: function () {//放大
        if (this.pdfConf.scale >= this.pdfConf.maxscale) {
          return
        }
        this.pdfConf.scale += 0.1;
        this.queueRenderPage(this.pdfConf.pageNum)
      },
      minus: function () {//缩小
        if (this.pdfConf.scale <= this.pdfConf.minscale) {
          return
        }
        this.pdfConf.scale -= 0.1;
        this.queueRenderPage(this.pdfConf.pageNum)
      },
    },
    mounted: function () {

      this.isPC = this.$store.getters.isPC;
      this.pdfConf.canvas = this.$refs.canvas;
      this.pdfConf.ctx = this.pdfConf.canvas.getContext('2d');
      PDFJS.workerSrc = '../assets/pdf/pdf.worker.js';
      let that = this;

      PDFJS.getDocument(this.$route.query.pdfUrl).then(function (pdfDoc_) {
        that.pdfConf.pdfDoc = pdfDoc_;
        that.pdfConf.totalNum = pdfDoc_.numPages;
        if (that.$route.query.index) {
          var page = Number(that.$route.query.index);
          that.pdfConf.pageNum = page;
        }
        // Initial/first page rendering
        that.renderPage(that.pdfConf.pageNum);
      });
    }
  }
</script>



