<template>
  <div class="content-wrapper">
    <!-- Content Header (Page header) -->
    <section class="content-header">
      <h1>
        评论
        <small>文件评论</small>
      </h1>
    </section>
    <section class="content">
      <div class="row">
        <div class="col-md-12">
          <!-- The time line -->
          <ul class="timeline">
            <!-- timeline time label -->
            <!--<li class="time-label">-->
            <!--<span class="bg-red">-->
            <!--2017.1.31-->
            <!--</span>-->
            <!--</li>-->

            <li v-for="item in commentList">
              <i class="fa fa-envelope bg-blue"></i>
              <div class="timeline-item">
                <span class="time"><i class="fa fa-clock-o"></i>{{item.create_at|filterCreateAt}}</span>

                <h3 class="timeline-header">{{item.fileid.filename|filterFilename}},第{{item.index}}页</h3>

                <div class="timeline-body" @click="getSingleUserComment(item)">
                  <img :src="item.commenter.headimgurl" class="head-img">
                  <p>来自<span class="text-info">{{item.commenter.nickname}}</span>的评论:</p>
                  {{item.comment}}
                </div>
                <div class="timeline-footer">
                  <a class="btn btn-success btn-xs" @click="view(item)">去看看</a>
                  <!--<a class="btn btn-danger btn-xs">Delete</a>-->
                </div>
              </div>
            </li>

            <li v-if="commentList&&commentList.length>0">
              <i class="fa fa-clock-o bg-gray" :class="{disabled:loading}"></i>
            </li>
          </ul>
          <clip-loader :loading="loading" size="80px"></clip-loader>
        </div>

        <!-- /.col -->
      </div>
      <div style="text-align: center" v-if="!commentList||commentList.length==0&&!loading">暂无任何评论</div>
    </section>
  </div>
</template>
<style>
  .head-img {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    overflow: hidden;
    border: solid #FFCCBA;
  }
</style>
<script>
  import ClipLoader from 'vue-spinner/src/ClipLoader.vue'
  export default {
    components: {
      ClipLoader
    },
    mounted:function () {
      this.getComment();
    },
    data(){
      return {
        commentList: [],
        pageIndex: 1,
        loading: false,
      }
    },

    watch: {
      $route(){
        this.getComment();
      }
    },
    filters: {
      filterFilename: function (name) {
        return name.substring(0, name.lastIndexOf("."));
      },
      filterCreateAt: function (date) {
        return date.replace('T', ' ').replace('Z', '').substring(0, date.lastIndexOf("."));
      },
    },
    methods: {
      getSingleUserComment:function (item) {
        this.loading = true;
        $.ajax({
          type: 'GET',
          url:this.$store.state.baseUrl + '/comments/user/58de53c121e7dc3aa8e48cb8/by/commenter/'+item.commenter._id+'/list',
          success: this.getCommentResult,
          error: this.getCommentResult
        })
      },
      getComment: function () {
        this.loading = true;
        $.ajax({
          type: 'GET',
          url: this.$route.query.type == 'unread' ? this.$store.state.baseUrl + '/pushs/subscriber/58de53c121e7dc3aa8e48cb8/unviewed/files' : this.$store.state.baseUrl + '/comments/user/58de53c121e7dc3aa8e48cb8/list',
          success: this.getCommentResult,
          error: this.getCommentResult
        })
      },
      getCommentResult: function (data) {
        console.log(data);
        this.loading = false;
        if (data.results)
          this.commentList = data.results;
        else
          this.commentList = [];
      },
      markAsViewed: function (item) {
        this.loading = true;
        $.ajax({
          type: 'DELETE',
          url: this.$store.state.baseUrl + '/comments/' + item._id + '/viewed',
          success: this.viewedResult,
          error: this.viewedResult
        })
      },
      viewedResult: function (data) {
        console.log(data);
      },
      view: function (item) {
        this.markAsViewed(item.fileid);
        if (item.fileid.fileType.indexOf('image') > -1) {
          this.$router.push({
            name: 'readImg',
            query: {imgUrl: this.$store.state.domain + item.fileid.key, fileId: item.fileid._id}
          })
        }
        else
          this.$router.push({
            name: 'readPdf',
            query: {
              pdfUrl: item.fileid.pdfKey ? this.$store.state.domain + item.fileid.pdfKey : this.$store.state.domain + item.fileid.key,
              fileId: item.fileid._id, index: item.index
            }
          })
      }
    }
  }

</script>



