<template>
  <div class="content-wrapper" style="margin-top: 20px;background-color: #ffffff!important;text-align:center">
    <img :src="$route.query.imgUrl" class="img preview-img" @click="preview"/>
    <div style="margin:10% 10% 0 10%">
      <div class="box box-primary direct-chat direct-chat-primary">
        <div class="box-header with-border">
          <h3 class="box-title">评论</h3>
        </div>
        <div class="box-body box-content">
          <div class="direct-chat-messages">
            <div class="direct-chat-msg right" v-for="item in commentList">
              <div class="direct-chat-info clearfix">
                <span class="direct-chat-name pull-right">{{item.commenter.nickname}}</span>
                <span class="direct-chat-timestamp pull-left">{{item.create_at|filterCreateAt}}</span>
              </div>
              <img class="direct-chat-img" :src="item.commenter.headimgurl" alt="Message User Image">
              <div class="direct-chat-text">
                {{item.comment}}
              </div>
            </div>
          </div>
        </div>
        <div class="box-footer">
          <div class="input-group">
            <input type="text" name="message" placeholder="请输入内容 ..." class="form-control" v-model='commentContent'>
            <span class="input-group-btn"><button type="button" class="btn btn-primary btn-flat"
                                                  @click='makeComment'>发送</button></span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style>
  .img {
    max-width: 40%;
  }
</style>
<script>
  export default {
    mounted: function () {
      this.getAllComment();
    },
    data(){
      return {
        commentList: [],
        commentContent: ''
      }
    },
    filters: {
      filterCreateAt: function (date) {
        return date.replace('T', ' ').replace('Z', '').substring(0, date.lastIndexOf("."));
      },
    },
    methods: {
      //获取某页文件的所有评论
      getAllComment: function () {
        $.ajax({
          type: 'GET',
          url: this.$store.state.baseUrl + '/comments/file/' + this.$route.query.fileId + "/1/list?pageIndex=1&pageSize=1000",
          success: this.getAllCommentResult,
          error: this.getAllCommentResult
        })
      },
      getAllCommentResult: function (data) {
        if (data.results)
          this.commentList = data.results;
      },
      //发起评论
      makeComment: function () {
        $.ajax({
          type: 'POST',
          url: this.$store.state.baseUrl + '/comments/file/' + this.$route.query.fileId + "/1",
          data: {
            commenterid: '58de53c121e7dc3aa8e48cb8',
            comment: this.commentContent
          },
          success: this.makeCommentResult,
          error: this.makeCommentResult
        })
      },
      makeCommentResult: function (data) {
        if (data._id) {
          this.getAllComment();
        }

      },
      preview: function () {
        var list = [];
        var item = {};
        item.src = this.$route.query.imgUrl;
        item.w = screen.width;
        item.h = document.body.offsetHeight;
        list.push(item);
        this.$preview.open(0, list);

      }
    }
  }
</script>



